@import '../../_base';
// Put all new css inside

.claims-container {
  width: 100%;
  height: auto;
  
  .make-img-overflow-hidden {
    overflow-x: hidden;
  
    @media screen and (min-width: 820px){
      overflow-x: initial;
    }
  }
    .claims-procedure {
      .title {
        position: relative;
        font-family: $font-open-sans;
        font-size: 36px;
        font-weight: 800;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: -0.1px;
        text-align: center;
        color: #354f53;
        // padding: 50px;
        margin: 50px;
        p {
          width: 100%;
          text-align: center;
        }
      }
      .flowchart {
        margin: 0 auto;
        padding: 30px;
        top: 170px;
        max-width: 800px;

        @media screen and (min-width: 992px) {
          max-width: 1000px;
        }
        @media screen and (min-width: 1440px) {
          max-width: 1200px;
        }
  
        .step-container {
          display: block;
          position: relative;
          height: auto;
          // width: 310px;

          @media screen and (min-width: 768px) {
            // width: 100%;
            height: 320px;
            // width: 310px;
          }
          @media screen and (min-width: 992px) {
            // width: 100%;
            height: 320px;
            width: 225px;
          }
          @media screen and (min-width: 1200px) {
            // width: 100%;
            height: 400px;
            // width: 310px;
          }
          img {
            position: relative;

            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0 auto;

            width: 130px;
            height: 130px;
            @media screen and (min-width: 768px) {
              width: 150px;
              height: 150px;
            }
          }
          .step-number {
            position: relative;
            display: flex;
            left: 10%;
            transform: translateY(-10px);

            opacity: 0.15;
            font-family: $font-open-sans;
            font-size: 60px;
            font-weight: 800;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: -0.2px;
            text-align: center;
            color: #00d7af;

            @media screen and (min-width: 320px) {
              left: 10%;
              bottom: 8%;
              font-size: 65px;
            }
            @media screen and (min-width: 500px) {
              left: 10%;
              bottom: 8%;
              font-size: 65px;
            }
            @media screen and (min-width: 768px) {
              left: 7%;
              bottom: 10%;
              font-size: 72px;
            }
            @media screen and (min-width: 992px) {
              left: 2%;
              bottom: 10%;
              font-size: 72px;
            }
          }
          .step-desc {
            position: relative;
            display: flex;
            // align-items: center;
            justify-content: center;
            margin: 0 auto;
            transform: translateY(-50px);

            width: 217px;
            height: 91px;
            font-family: $font-open-sans;
            font-size: 14px;
            font-weight: normal;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: -0.1px;
            text-align: center;
            color: #354f53;

            @media screen and (min-width: 768px) {
              font-size: 16px;
              transform: translateY(-90px);
            }
          }
        }
      }
      .line {
        position: absolute;
        left: 203px;
        bottom: 245px;
        width: 40px;
        height: 2px;
        border-style: solid;
        border-width: 2px;
        border-image-source: linear-gradient(91deg, #00d7af, #46737a);
        border-image-slice: 1;
        display: none;

        @media screen and (min-width: 992px) {
          left: 203px;
          bottom: 245px;
          display: initial;
        }
        
        @media screen and (min-width: 1200px) {
          left: 203px;
          bottom: 325px;
          display: initial;
        }

        @media screen and (min-width: 1440px) {
          left: 233px;
          bottom: 325px;
          width: 90px;
          display: initial;
        }
      }
      .line-4 {
        display: none;
      }
    }
    .fillings-guide {
      position: relative;
      height: auto;
      // width: 1440px;
      width: 100%;
      object-fit: contain;
      z-index: 2;
      .title {
        position: absolute;
        width: 100%;
        height: 49px;
        font-family: $font-open-sans;
        font-size: 36px;
        font-weight: 800;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: -0.1px;
        text-align: center;
        color: #354f53;
      }
      .sub-title {
        position: relative;
        width: 100%;
        height: 22px;
        font-family: $font-open-sans;
        font-size: 16px;
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: 0px;
        text-align: center;
        color: #46737a;
        padding: 50px;
      }
      .category-section {
        margin-top: 10px;
        // position: absolute;
        // top: 115px;
        // left: 217px;
        .category-selected-block {
          cursor: pointer;
          outline: none;
          width: 323px;
          height: 84px;
          border-radius: 29px;
          box-shadow: 0 20px 15px 0 rgba(70, 115, 122, 0.15);
          border: solid 2px transparent;
          background-image: linear-gradient(#ffffff, #ffffff), linear-gradient(103deg, #00d7af, #46737a);
          background-origin: border-box;
          background-clip: content-box, border-box;
          margin: 12px auto;
          img {
            position: relative;
            display: inline-block;
            width: 50px;
            height: 100%;
            object-fit: contain;
            vertical-align: middle;
            left: 38px;
          }
          p {
            display: inline;
          }
          .category-desc {
            position: relative;
            left: 80px;

            width: 54px;
            height: 24px;
            object-fit: contain;
            font-family: $font-open-sans;
            font-size: 18px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: -0.2px;
            color: #354f53;
          }
        }
        .category-block {
          cursor: pointer;
          outline: none;
          width: 323px;
          height: 84px;
          border-radius: 29px;
          background-color: #ffffff;
          box-shadow: 0 20px 15px 0 rgba(70, 115, 122, 0.15);
          margin: 12px auto;
          img {
            position: relative;
            display: inline-block;
            width: 50px;
            height: 100%;
            object-fit: contain;
            vertical-align: middle;
            left: 38px;
          }
          p {
            display: inline;
          }
          .category-desc {
            position: relative;
            left: 80px;

            width: 54px;
            height: 24px;
            object-fit: contain;
            font-family: $font-open-sans;
            font-size: 18px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: -0.2px;
            color: #354f53;
          }
        }
      }
      .document-section {
        position: relative;
        margin-top: 15px;
        .title {
          position: relative;
          height: 30px;
          font-family: $font-open-sans;
          font-size: 24px;
          font-weight: 800;
          font-style: normal;
          font-stretch: normal;
          line-height: normal;
          letter-spacing: -0.1px;
          text-align: left;
          color: #354f53;
        }
        .sub-title {
          position: relative;
          width: 100%;
          padding: 0;
          font-family: $font-open-sans;
          font-size: 16px;
          font-weight: normal;
          font-style: normal;
          font-stretch: normal;
          line-height: normal;
          letter-spacing: 0px;
          text-align: left;
          color: #46737a;
        }
        .line {
          margin-top: 20px;
          margin-bottom: 20px;
          width: 584px;
          height: 1px;
          background-color: #c8e0e4;
        }
        .document-list {
          width: 100%;
          // height: auto;
          margin-bottom: 8%;
          .document-item {
            // display: block;
            margin-top: 23px;
            margin-bottom: 23px;
            text-align: center;
            width: 100%;
            // height: 60px;
            .item-number {
              top: 10px;
              display: inline-block;
              position: relative;
              width: 30px;
              height: 30px;
              border-radius: 50%;
              background-image: linear-gradient(132deg, #00d7af, #46737a);
              .number {
                position: relative;
                top: 2px;
                width: 11px;
                height: 24px;
                font-family: $font-open-sans;
                font-size: 18px;
                font-weight: 800;
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: 0px;
                color: #ffffff;
                text-align: center;
              }
            }
            .item-icon {
              // margin-left: 24px;
              // margin-right: 24px;
              width: 40px;
              height: 50px;
              @media screen and (max-width: 1050px) {
                display: none !important;
              }
            }
            .item-desc {
              position: relative;
              left: 15px;
              display: flex;
              align-items: center;
              text-align: left;
              width: 88%;
              height: 50px;
              font-family: $font-open-sans;
              font-size: 18px;
              font-weight: normal;
              font-style: normal;
              font-stretch: normal;
              line-height: normal;
              letter-spacing: 0px;
              color: #46737a;

              @media screen and (max-width: 419px) {
                // display: none !important;
                position: relative;
                bottom: 30px;
                left: 50px;
              }
            }
          }
        }
        .company-list {
          display: block;
          .company {
            cursor: pointer;
            overflow: hidden;
            margin-left: 22px;
            margin-right: 22px;
            margin-bottom: 36px;
            display: inline-block;
            width: 156px;
            height: 156px;
            border-radius: 29px;
            background-color: #ffffff;
            box-shadow: 0 20px 15px 0 rgba(70, 115, 122, 0.15);

            left: 20%;
            @media screen and (min-width: 350px) {
              left: 25%;
            }
            @media screen and (min-width: 400px) {
              // position: relative;
              left: 0px;
            }
            .img-container {
              width: 100px;
              height: 100px;
              display:flex;
              align-items:center;
              justify-content:center;
              img {
                position: relative;
                top: 28px;
                left: 14px;
                width: 100%;
                height: auto;
              }
            }
          }
        }
      }
    }
  }
